import React from "react";

import Layout from "components/layout";
import Hero from "components/learningCenter/hub/hero";
import Intro from "components/learningCenter/hub/intro";
import Footer from "components/learningCenter/hub/footer";
import SimpleValueProps from "components/learningCenter/hub/simpleValueProps";

import colors from "styles/colors";

type Content = { [key: string]: string };

interface Props {
  location: Location;
  pageContext: {
    contentfulData: {
      heroReference: {
        fields: {
          title: string;
          subtitle: string;
          ctaSubtitle: string;
          image: Object;
        };
      };
      metaTitle: string;
      metaDescription: string;
      contentReference: Array<Content>;
      content: Content;
    };
    categoryData: {
      fields: {
        metaTitle: string;
        metaDescription: string;
        categories: [
          {
            fields: {
              title: string;
              subTitle: string;
              description: string;
              categoryPathName: string;
            };
          },
        ];
      };
    };
  };
}

const HubLearningCenterTemplate = ({
  pageContext: { contentfulData, categoryData },
  location,
}: Props) => {
  const keys = Object.keys(contentfulData);
  const categories = categoryData.fields.categories;
  return (
    <Layout
      location={location}
      title={categoryData.fields.metaTitle}
      description={categoryData.fields.metaDescription}
      whiteContentBackground
      whiteGutterBackground
    >
      <Hero gutterBackground={colors.black} />
      <Intro />
      {keys.map((key) => (
        <SimpleValueProps
          articles={contentfulData[key]}
          key={key}
          category={
            categories.filter(
              (filter) => filter.fields.categoryPathName === key,
            )[0]
          }
        />
      ))}
      <Footer />
    </Layout>
  );
};
export default HubLearningCenterTemplate;
