import React from "react";

import { Row, Column } from "components/Grid";

import InternalLinkPrimaryButton from "components/reusable/atoms/buttons/InternalLinkPrimaryButton";

import arrow from "assets/img/icons/orange/link-arrow.svg";
import {
  CaptionWrapper,
  Title,
  Body,
  ButtonWrapper,
  Paragraph,
  FooterContainer,
  FooterItemContainer,
  Image,
  LinkArrow,
  Container,
  ButtonWrapperMobile,
  ImageTitle,
  TitleWrapper,
} from "./elements";

type ArticleProps = [
  {
    fields: {
      urlPath: string;
      category: string;
      heroReference: {
        fields: {
          title: string;
          subTitle: string;
          category: string;
          urlPath: string;
        };
      };
      thumbnailImage: {
        fields: {
          file: {
            url: string;
          };
        };
      };
    };
  },
];

type CategoryProps = {
  fields: {
    title: string;
    subTitle: string;
    description: string;
  };
};

interface Props {
  articles: ArticleProps;
  category: CategoryProps;
}

const MosaicValueProps = ({ articles, category }: Props) => (
  <Container>
    <Row padding={30} gutterWidth={26}>
      <Column span={1} spanMedium={1} spanSmall={0} />
      <Column span={4} spanMedium={10} spanSmall={12}>
        <CaptionWrapper>
          <TitleWrapper>
            <Title to={`/learn/${articles[0].fields.category}`}>
              {category.fields.title}
            </Title>
          </TitleWrapper>
          <Body>{category.fields.subTitle}</Body>
          <ButtonWrapper>
            <InternalLinkPrimaryButton
              to={`/learn/${articles[0].fields.category}`}
            >
              browse all articles
            </InternalLinkPrimaryButton>
          </ButtonWrapper>
        </CaptionWrapper>
      </Column>
      <Column span={0} spanMedium={1} spanSmall={0} />
      <Column span={1} spanMedium={1} spanSmall={0} />
      <Column span={5} spanMedium={10} spanSmall={12}>
        <Paragraph>{category.fields.description}</Paragraph>
        <ButtonWrapperMobile>
          <InternalLinkPrimaryButton
            to={`/learn/${articles[0].fields.category}`}
          >
            browse all articles
          </InternalLinkPrimaryButton>
        </ButtonWrapperMobile>
      </Column>
      <Column span={1} spanMedium={1} spanSmall={0} />
    </Row>
    <Row padding={30} gutterWidth={26}>
      <Column span={1} spanMedium={1} spanSmall={0} />
      <Column span={10} spanMedium={10} spanSmall={12}>
        <FooterContainer>
          {articles.map(
            (article, index: number) =>
              index <= 5 && (
                <FooterItemContainer
                  key={article.fields.urlPath}
                  index={index}
                  to={
                    `learn/${article.fields.category}/${article.fields.urlPath}` ||
                    "/"
                  }
                >
                  <LinkArrow>
                    <img src={arrow} alt="arrow" />
                  </LinkArrow>
                  {article.fields.thumbnailImage &&
                    article.fields.thumbnailImage.fields.file && (
                      <Image
                        src={article.fields.thumbnailImage.fields.file.url}
                      />
                    )}
                  <ImageTitle>
                    {article.fields.heroReference.fields.title}
                  </ImageTitle>
                </FooterItemContainer>
              ),
          )}
        </FooterContainer>
      </Column>
      <Column span={1} spanMedium={1} spanSmall={0} />
    </Row>
  </Container>
);

export default MosaicValueProps;
